*{
    font-family: 'Poppins', sans-serif;
  }
  .navbar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 60px;
    background-color: rgb(2, 12, 32);
    display: flex;
    align-items: center;
    justify-content: space-around;
    
  }
  .logo{
    font-size: 26px;
    color:rgb(255, 255, 255);
    display: flex;
    text-shadow: 1px 1px rgb(56 182 255);
    align-items: center;
  }
  
  .logo>span{
    font-size: 30px;
    color:rgb(212, 0, 255) ;
  }
  
  .nav-links {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 70%;
  }
  .nav-linksd {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 70%;
  }
  .home,
  .contact {
    text-decoration: none;
    color: #fff;
    font-size: 18px;
    padding: 10px 20px;
    cursor: pointer;
  }
  .home:hover{
    color: rgb(56 182 255);
  }
  .contact {
    color: rgb(56 182 255);
    
    background-color: rgb(2, 12, 32);
  }
  .contact:hover {
    color: rgb(56 182 255);
    border: 1px solid rgb(56 182 255);
    background-color:  rgb(2, 12, 32);
  }
  
  .mobai-menu-icon {
    display: none;
  }
  @media screen and (max-width: 780px) {
    .logo{
      display: flex;
      align-items: center;
      position: absolute;
      font-size: 26px;
      text-shadow: 1px 1px rgb(56 182 255);
      color:#fff;
      left: 35px;
    }
    
    .logo>span{
      font-size: 30px;
      color:rgb(212, 0, 255); 
    }
    .mobai-menu-icon {
      display: block;
    }
    .nav-links {
      display: none;
    }
    .nav-linksd {
      position: absolute;
      display: block;
      list-style: none;
      background-color:rgb(48 54 66);
      left: 0;
      top: 60px;
      transition: all 0.5s ease-out;
      width: 100%;
    }
    .home,
    .contact {
      color: #fff;
      text-align: center;
      padding: 32px;
      transition: all 0.5s ease;
    }
    .contact {
      color: none;
      background-color: rgb(56 182 255);
    }
    .mobai-menu-icon{
      height: 35px;
      width: 35px;
      display: block;
      position: absolute;
      font-size: 25px;
      color:rgb(207, 207, 240);
      background-color:rgb(48 54 66);
      border: 0ch;
      top: 15px;
      right: 25px;
    }
  }
  